@import url("https://fonts.googleapis.com/css?family=Frijole|Lato:400,700");
@import url("https://fonts.googleapis.com/css?family=Lato:400,700|Oswald:600");
article, aside, details, figcaption, figure, footer, header, hgroup, nav, section, summary {
  display: block; }

audio, canvas, video {
  display: inline-block; }

audio:not([controls]) {
  display: none;
  height: 0; }

[hidden] {
  display: none; }

html {
  font-family: sans-serif;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%; }

a:focus {
  outline: thin dotted; }

a:active, a:hover {
  outline: 0; }

h1 {
  font-size: 2em; }

abbr[title] {
  border-bottom: 1px dotted; }

b, strong {
  font-weight: 700; }

dfn {
  font-style: italic; }

mark {
  background: #ff0;
  color: #000; }

code, kbd, pre, samp {
  font-family: monospace, serif;
  font-size: 1em; }

pre {
  white-space: pre-wrap;
  word-wrap: break-word; }

q {
  quotes: 01C 01D 018 019; }

small {
  font-size: 80%; }

sub, sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline; }

sup {
  top: -.5em; }

sub {
  bottom: -.25em; }

img {
  border: 0; }

svg:not(:root) {
  overflow: hidden; }

fieldset {
  border: 1px solid silver;
  margin: 0 2px;
  padding: .35em .625em .75em; }

button, input, select, textarea {
  font-family: inherit;
  font-size: 100%;
  margin: 0; }

button, input {
  line-height: normal; }

button, html input[type=button], input[type=reset], input[type=submit] {
  -webkit-appearance: button;
  cursor: pointer; }

button[disabled], input[disabled] {
  cursor: default; }

input[type=checkbox], input[type=radio] {
  box-sizing: border-box;
  padding: 0; }

input[type=search] {
  -webkit-appearance: textfield;
  -moz-box-sizing: content-box;
  -webkit-box-sizing: content-box;
  box-sizing: content-box; }

input[type=search]::-webkit-search-cancel-button, input[type=search]::-webkit-search-decoration {
  -webkit-appearance: none; }

textarea {
  overflow: auto;
  vertical-align: top; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

body, figure {
  margin: 0; }

legend, button::-moz-focus-inner, input::-moz-focus-inner {
  border: 0;
  padding: 0; }

.clearfix:after {
  visibility: hidden;
  display: block;
  font-size: 0;
  content: '';
  clear: both;
  height: 0; }

* {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box; }

body {
  margin: 0;
  padding: 0;
  font-family: "Lato", sans-serif;
  color: #ffff;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: url(/assets/background.jpg);
  background-size: cover;
  background-repeat: no-repeat; }

main {
  background: url(/assets/shapes.png);
  background-size: contain;
  background-repeat: no-repeat; }

.wrapper {
  width: 80%;
  max-width: 1200px;
  margin: 0 auto; }

.display-none {
  display: none; }

img {
  display: block;
  width: 100%; }

.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

.Header__Component h1 {
  margin-top: 120px;
  font-family: "Frijole", cursive;
  font-size: 85px;
  width: 50%; }

@media (max-width: 768px) {
  .Header__Component h1 {
    font-size: 55px;
    text-align: center;
    width: 100%; } }

.forms {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap; }
  .forms .Auth__Component {
    margin-top: 100px;
    width: 50%;
    text-align: center;
    flex: 1 0 350px; }
    .forms .Auth__Component h4 {
      font-size: 25px;
      text-transform: uppercase;
      font-weight: 700;
      letter-spacing: 2.6px; }
    .forms .Auth__Component form {
      display: flex;
      flex-direction: column;
      align-items: center;
      margin-top: 40px;
      font-size: 20px; }
    .forms .Auth__Component label {
      width: 380px;
      text-align: left;
      padding: 10px 0; }
    .forms .Auth__Component input {
      border: none;
      background-color: transparent;
      border-bottom: 2px solid white;
      padding: 10px;
      margin: 10px 0 25px;
      text-transform: lowercase;
      width: 380px;
      color: #ffe900; }
    .forms .Auth__Component input::placeholder {
      color: #ffe900; }
    .forms .Auth__Component input[type="submit"] {
      width: 230px;
      background-color: #ffe900;
      border: none;
      border-radius: 40px;
      color: #143307;
      text-transform: uppercase;
      font-weight: 700; }
      .forms .Auth__Component input[type="submit"]:hover {
        background: #fff480; }

.Token__Component {
  margin-top: 220px;
  text-align: center; }
  .Token__Component h2 {
    font-size: 30px;
    letter-spacing: 2.4px; }
  .Token__Component .token-details {
    text-align: center;
    padding: 20px 80px;
    font-size: 20px;
    border-radius: 40px;
    min-width: 400px;
    margin: 0 auto;
    color: #ffe900;
    background-color: #143307;
    overflow-wrap: break-word; }

.apiDoc {
  background-color: #143307;
  border-radius: 50px;
  margin-top: 150px; }
  .apiDoc h3 {
    font-size: 30px;
    padding-top: 50px;
    padding-left: 50px;
    padding-bottom: 20px;
    border-bottom: 1px solid #ffe900;
    text-transform: uppercase;
    letter-spacing: 2.4px; }
  .apiDoc .ApiDoc__Component {
    padding-left: 50px;
    padding-bottom: 50px;
    font-size: 25px; }
    .apiDoc .ApiDoc__Component .requestHeadline {
      letter-spacing: 2px; }
    .apiDoc .ApiDoc__Component .requestUrlContainer {
      font-size: 20px;
      display: flex;
      border: 2px solid #ffe900;
      border-radius: 50px;
      width: fit-content;
      padding: 20px;
      position: relative; }
      .apiDoc .ApiDoc__Component .requestUrlContainer h5 {
        margin: 0; }
      .apiDoc .ApiDoc__Component .requestUrlContainer .requestType {
        background-color: #ffe900;
        color: #143307;
        position: absolute;
        padding: 20px;
        top: 0;
        left: 0;
        border-top-left-radius: 50px;
        border-bottom-left-radius: 50px;
        flex-shrink: 0; }
      .apiDoc .ApiDoc__Component .requestUrlContainer .requestURL {
        margin-left: 75px; }
      @media (max-width: 768px) {
        .apiDoc .ApiDoc__Component .requestUrlContainer .requestURL {
          margin: 0;
          text-align: center; }
        .apiDoc .ApiDoc__Component .requestUrlContainer .requestType {
          top: -20px;
          border-radius: 50px;
          width: 100%;
          text-align: center;
          padding: 0;
          clip-path: inset(0px 20px 1px 20px); } }
    .apiDoc .ApiDoc__Component table {
      width: 100%;
      text-align: left; }
      .apiDoc .ApiDoc__Component table th {
        font-size: 20px;
        padding-bottom: 15px;
        border-bottom: 2px solid black; }
      .apiDoc .ApiDoc__Component table td {
        padding-top: 20px;
        font-size: 20px; }
  @media (max-width: 768px) {
    .apiDoc h3 {
      text-align: center;
      padding-left: 0; }
    .apiDoc .requestUrlContainer {
      margin: 0 auto; }
    .apiDoc .ApiDoc__Component {
      text-align: center;
      padding-left: 0; }
      .apiDoc .ApiDoc__Component table {
        text-align: center; }
        .apiDoc .ApiDoc__Component table .unseen {
          display: none; } }

.Footer__Component {
  font-family: 'Lato', sans-serif;
  width: 100%;
  background-color: #000;
  color: white;
  height: 35px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 13px;
  font-weight: 700; }
